<!--  router-link  path  -->
<template>
<div>
  <div class="sideBar">
    <div class="sideList">
      <div class="accordion" id="accordion2">
        <div class="accordion-group slideWrap"  v-for="(outeritem,index) in slideList">
          <div class="accordion-heading slideHead">
            <!---->
            <a class="accordion-toggle" data-toggle="collapse" data-parent="#accordion2"  :href=" '#'+index" >
              {{outeritem.title}}
            </a>
          </div>
          <div :id='index' class="accordion-body collapse in">
            <div  class="accordion-inner slideInner"  v-for="(inneritem,innerindex) in outeritem.nextmemu" :class="{ block: (index === 0) }">
              <router-link exact :to="{path:'/bs/'+outeritem.msg+'/'+inneritem.info}" style="color: #666" >
                {{inneritem.menu}}
              </router-link>
            </div>
          </div>
        </div>

      </div>
    </div>
  </div>
  <!---->
  <div class="rightInfos">
    <transition  mode="out-in">
      <router-view  name="center"></router-view>
    </transition>
  </div>
</div>
</template>

<script>
  //   

  var data=[//ajax
    {
      title:"",//
      msg:"ggzy",
      nextmemu:[
        {menu:"",info:"sw1"},
        {menu:"",info:"sw2"}
      ]
    },
    {
      title:"",
      msg:"khdw",
      nextmemu:[
        {menu:"",info:"sw3"}
      ]
    },
    {
      title:"",
      msg:"tjkh",
      nextmemu:[
        {menu:"",info:"sw4"},
        {menu:"2",info:"sw4"}
      ]
    }
  ];

  //
  export default {
    data: function () {
      return {
        slideList:data,
        slideInfo:{},
        isBlock:false
      }
    },
    watch:{
      $route: function () {
        // $route 

      }
    },
    created: function () {
      // 
      // 
    },
    methods:{

    }
  }

</script>
<style>
  .sideList dd.block{display: block}
</style>



