<template>
    <div>
      <div class="mt50" style="font-size: 30px;color:black">
         :
      </div>
      <input type="text" placeholder="" v-model='getVal'>

      <drop :data='sportList' v-on:getData="handleData">
        <div slot='header'>
          
        </div>
        <div slot='footer'>
          
        </div>
      </drop>
        <br>
        <label for=""></label>
        <input type="text" 
         v-model="email" 
        :class="{borderRed:isEmailShow}" 
         placeholder="" 
         @input='checkInput()'>
        <div style='color:red' v-show="isEmailShow">
          {{emailTip}}
        </div>
    </div>
</template>
<script>
  //
  //    export 
  import drop from "@/components/dropDown"
  import {Regexs} from "@/utils/index"
  export default {
    data: function () {
      return {
         getVal:'',
         email:'',
         emailTip:'',
         isEmailShow:false,//
         sportList:[
         {
          name:'javascript'
         },{
          name:'html'
         },{
          name:'css'
         }]
      }
    },
    components:{
      drop,
    },
    methods:{
      checkInput(){
        if(this.email == ""){
           this.isEmailShow = true;
          this.emailTip = "";
        } else if(Regexs.Email.test(this.email)){
          this.isEmailShow = false;
          this.emailTip = "~~~"
          return true;
        }else{
          this.isEmailShow = true;
          this.emailTip = "";
          return false;//
        }
      },
      handleData(val){//
        // alert(val)
        this.getVal = val;
      }
    }
  }

</script>
<style>
.borderRed{border:1px solid red;}
</style>



