<template>
  <div class="container body-content">
    <div id="test" class="form-group">
      <div class="form-group">
          <div class="page-header">
            
          </div>
        <form class="form-inline" style="margin-bottom: 30px;">
          <div class="form-group">
            <input type="text" placeholder="" class="form-control" v-model="searchkey"/>
            <button type="button" class="btn btn-primary" @click="searchData"></button>
          </div>
          <div class="form-group">
            <button type="button" class="btn btn-danger" @click="resetData()"></button>
          </div>
          <div class="form-group">
            <button type="button" class="btn btn-primary" data-toggle="modal"  @click="showModal()">
              
            </button>
          </div>

        </form>
        <table class="table table-bordered table-responsive table-striped table-hover">
          <thead>
          <tr class="text-center">
            <th class="text-center"></th>
            <th class="text-center"></th>
            <th class="text-center"></th>
          </tr>
          </thead>
          <tbody>
          <!--<tr v-for="(item,index) in filterArrayData">-->
          <tr v-for="(item,index) in arrayData">
            <td width="40%" class="text-center">{{item.name}}</td>
            <td width="30%">{{item.age}}</td>
            <td>
              <a href="javascript:void(0)">
                   <button class="btn btn-primary btn-sm" @click="showModalEdit(index,item.name,item.age)" data-toggle="modal"></button>
                   <button class="btn btn-warning btn-sm" @click="showModalDel(index,item.age)" data-toggle="modal"></button>
               </a>
            </td>
          </tr>
          </tbody>
        </table>
        <div class="pager" id="pager">
          <!---->
          <span class="form-inline">
              <select class="form-control" v-model="pagesize" @change="showPage(pageCurrent,$event,true)" number>
                <option value="10">10</option>
                <option value="20">20</option>
                <option value="30">30</option>
                <option value="40">40</option>
              </select>
          </span>
          <!---->
          <template v-for="item in pageCount+1">
                        <span v-if="item==1" class="btn btn-default" v-on:click="showPage(1,$event)">
                            
                        </span>
                        <span v-if="item==1" class="btn btn-default" v-on:click="showPage(pageCurrent-1,$event)">
                            
                        </span>
                        <span v-if="item==1" class="btn btn-default" v-on:click="showPage(item,$event)">
                            {{item}}
                        </span>
                        <span v-if="item==1&&item<showPagesStart-1" class="btn btn-default disabled">
                            ...
                        </span>
                        <span v-if="item>1&&item<=pageCount-1&&item>=showPagesStart&&item<=showPageEnd&&item<=pageCount" class="btn btn-default" v-on:click="showPage(item,$event)">
                            {{item}}
                        </span>
                        <span v-if="item==pageCount&&item>showPageEnd+1" class="btn btn-default disabled">
                            ...
                        </span>
                        <span v-if="item==pageCount" class="btn btn-default" v-on:click="showPage(item,$event)">
                            {{item}}
                        </span>
                        <span v-if="item==pageCount" class="btn btn-default" v-on:click="showPage(pageCurrent+1,$event)">
                            
                        </span>
                        <span v-if="item==pageCount" class="btn btn-default" v-on:click="showPage(pageCount,$event)">
                            
                        </span>
          </template>

          <!---->
           <span class="form-inline">
                <input class="pageIndex form-control"
                       style="width:60px;text-align:center"
                       type="text"
                       v-model="pageCurrent"
                       @keyup.enter="showPage(pageCurrent,$event,true)"
                  />
               </span>
          <span>{{pageCurrent}}/{{pageCount}}</span>
          </div>
      </div>
    </div>
    <!-- Add Modal -->
    <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
            <h4 class="modal-title" id="myModalLabel"></h4>
          </div>
          <div class="modal-body">
            <form>
              <div class="form-group text-left">
                <label for="recipient-name" class="control-label">:</label>
                <input type="text" class="form-control" id="recipient-name"  v-model="addName">
              </div>
              <div class="form-group text-left">
                <label for="recipient-age" class="control-label" >:</label>
                <input type="text" class="form-control" id="recipient-age" v-model="addAge">
              </div>

            </form>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-default" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="saveAddData()"></button>
          </div>
          </div>
        </div>
      </div>

    <!--edit Model -->

    <editModal v-if="isShowEdit" mess="EditModal" :EditingNam="EditingName" :EditingAg="EditingAge"  :EditingInde="EditingIndex" v-on:editMsg="handleEdit" v-on:closeEditModal="handleClose"></editModal>

    <!--Delete Modal-->
    <div role="dialog" class="modal fade bs-example-modal-sm" id="layer">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal">
              <span>&times;</span>
            </button>
            <h4 class="modal-title">?</h4>
          </div>
          <div class="modal-body text-right">
            <button data-dismiss="modal" class="btn btn-primary btn-sm"></button>
            <button data-dismiss="modal" class="btn btn-danger btn-sm" @click="deleteMsg(nowIndex)"></button>
          </div>
        </div>
      </div>
    </div>
    </div>
   </div>
</template>
<script>
  import editModal from './editModal.vue'
  var arrayData=[
    {
      "name":"zhangsan",
      "age":12
    },
    {
      "name":"liqin0",
      "age":12
    },
    {
      "name":"liqin1",
      "age":12
    },
    {
      "name":"liqin2",
      "age":12
    }
    ,
    {
      "name":"liqin3",
      "age":12
    },
    {
      "name":"liqin4",
      "age":12
    },
    {
      "name":"liqin5",
      "age":12
    },
    {
      "name":"liqin6",
      "age":12
    },
    {
      "name":"liqin7",
      "age":12
    },
    {
      "name":"liqin8",
      "age":12
    },
    {
      "name":"liqin9",
      "age":11
    },
    {
      "name":"wang",
      "age":20
    }
  ];
  export default {
    data: function () {
      return {
        //
        totalCount: 200,
        //
        pageCount: 20,
        //
        pageCurrent: 1,
        //
        pagesize: 10,
        //
        showPages: 5,
        //
        showPagesStart: 1,
        //
        showPageEnd: 100,
        //
        arrayData: arrayData,

        //
        searchname:"",
        searchage:"",
        searchkey:"",

        //
        addName:"",
        addAge:"",


        // 
        nowIndex:-100,


        //
        EditingIndex:-100,
        EditingName:"",
        EditingAge:"",

        //
        newPageInfo:[],

        //
        isShowEdit:false
      }
    },
    components: {
      editModal:editModal
    },
    computed: {//
      filterArrayData: function () {
        var self = this;
        return self.arrayData.filter(function (item) {
          return item.name.indexOf(self.searchname) !== -1
        })
      }
    },
    watch: {
      $route: function () {

      }
    },
    created: function () {
      this.showPage(this.pageCurrent, null, true);
    },
    methods: {
      searchData:function(){//
        var that = this;
        var tempData = this.arrayData;//
        if(this.searchkey != "") {
          this.arrayData = [];//
          tempData.forEach(function (elem) {//item
            for (var i in elem) {
              if (elem[i].toString().indexOf(that.searchkey) > -1) {//  toString  indexOf
                that.arrayData.push(elem);
                return;
              }
            }
          })
        }else {
          this.arrayData=tempData;
          console.log(tempData)
        }
      },
      handleClose: function () {//
        $('#EditModal').modal('hide');
        this.isShowEdit=false;
      },
      handleEdit: function () {
        var newName=arguments[0];
        var newAge=arguments[1];
        var this_index=arguments[2];
        //  
        this.arrayData[this_index].name=newName;
        this.arrayData[this_index].age=newAge;
        $('#EditModal').modal('hide');
        this.isShowEdit=false;
      },
      showModalDel:function(index){//
        $('#layer').modal('show');
        this.nowIndex=index;
      },
      showModalEdit: function (_index,name,age) {//
        this.isShowEdit=true;
        this.EditingName=name;
        this.EditingAge=age;
        this.EditingIndex=_index;
        window.setTimeout(function () {
          $('#EditModal').modal('show');
        },200);
      },
      deleteMsg: function (index) {
          this.arrayData.splice(index,1);
      },
      showModal: function () {//  push  this
        this.addName="";//
        this.addAge="";
        $('#myModal').modal('show');
      },
      saveAddData: function () {//
        if(this.addName && this.addAge){
          console.log(this.addName+","+this.addAge);//this
          this.arrayData.unshift({
            "name":this.addName,
            "age":this.addAge
          })
        }
        $('#myModal').modal('hide');
      },
      resetData: function () {
       //
        this.searchname="";//
      },
      showPage: function (pageIndex, $event, forceRefresh) {//




        var This=this;
        if (pageIndex > 0) {
          if (pageIndex > this.pageCount) {
            pageIndex = this.pageCount;
          }
          //
          var currentPageCount = Math.ceil(this.totalCount / this.pagesize);
          if (currentPageCount != this.pageCount) {
            pageIndex = 1;
            this.pageCount = currentPageCount;
          }
          else if (this.pageCurrent == pageIndex && currentPageCount == this.pageCount && typeof (forceRefresh) == "undefined") {
            console.log("not refresh");
            return;
          }
          //
          var buttons = $("#pager").find("span");
          for (var i = 0; i < buttons.length; i++) {
            if (buttons.eq(i).html() != pageIndex) {
              buttons.eq(i).removeClass("active");
            }
            else {
              buttons.eq(i).addClass("active");
            }
          }

          // 
          var newPageInfo = [];
          for (var i = 0; i < this.pagesize; i++) {
           newPageInfo[newPageInfo.length] = {
           name: "test" + (i + (pageIndex - 1) * 20),
           age: (i + (pageIndex - 1) * 20)
           };
           }
          var that=this;
          function getData(){//  slice
            that.$http.get('../js/data.json',{
              pageIndex:pageIndex,
              pagesize:This.pagesize
            }).then(function(res){
              newPageInfo = newPageInfo.concat(res.data);
              console.log(newPageInfo);//
              This.pageCurrent = pageIndex;
              This.arrayData = newPageInfo;
            },function(res){
              alert(res.status);
            });
          }
          //getData();

           this.pageCurrent = pageIndex;
           this.arrayData = newPageInfo;

          // 20 5
          if (this.pageCount > this.showPages) {
            if (pageIndex <= (this.showPages - 1) / 2) {
              this.showPagesStart = 1;
              this.showPageEnd = this.showPages - 1;
              console.log("showPage1")
            }
            else if (pageIndex >= this.pageCount - (this.showPages - 3) / 2) {
              this.showPagesStart = this.pageCount - this.showPages + 2;
              this.showPageEnd = this.pageCount;
              console.log("showPage2");
            }
            else {
              console.log("showPage3");
              this.showPagesStart = pageIndex - (this.showPages - 3) / 2;
              if(!isNaN(pageIndex)){//

              }else{

              }
              this.showPageEnd = Number(pageIndex) + (Number(this.showPages) - 3) / 2;//
            }
          }
          console.log("showPagesStart:" + this.showPagesStart + ",showPageEnd:" + this.showPageEnd + ",pageIndex:" + pageIndex);
        }
      }
    }

  }

</script>
<style>
</style>



