<!--  router-link  path  -->
<template>
  <div>
    <div class="sideBar">
      <div class="sideList">
        <div class="accordion" id="accordion2">
          <div class="accordion-group slideWrap"  v-for="(outeritem,index) in slideList">
            <div class="accordion-heading slideHead">
              <!---->
              <a class="accordion-toggle" data-toggle="collapse" data-parent="#accordion2"  :href=" '#'+index" >
                {{outeritem.title}}
              </a>
            </div>
            <div :id='index' class="accordion-body collapse in">
              <div  class="accordion-inner slideInner"  v-for="(inneritem,innerindex) in outeritem.nextmemu" :class="{ block: (index === 0) }">
                <router-link exact :to="{path:'/order/'+outeritem.msg+'/'+inneritem.info}" style="color: #666" >
                  {{inneritem.menu}}
                </router-link>
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>
    <!---->
    <div class="rightInfos">
      <transition  mode="out-in">
        <router-view  name="center"></router-view>
      </transition>
    </div>
  </div>
</template>

<script>
  //   

  var data=[//ajax
    {
      title:"",//
      msg:"ddgl",
      nextmemu:[
        {menu:"",info:"order1"},
        {menu:"",info:"order2"},
        {menu:"",info:"order3"},
        {menu:"",info:"order4"},
        {menu:"",info:"order5"},
        {menu:"",info:"order6"},

        {menu:"",info:"order7"},
        {menu:"",info:"order8"},
        {menu:"",info:"order9"},
        {menu:"",info:"order10"},
        {menu:"ZTS()",info:"order11"},
        {menu:"ZTS()",info:"order12"},
        {menu:"ZTS",info:"order13"}
      ]
    },
    {
      title:"",
      msg:"jsd",
      nextmemu:[
        {menu:"",info:"order14"},
        {menu:"",info:"order15"},
        {menu:"",info:"order16"},
        {menu:"",info:"order17"},
        {menu:"",info:"order18"},
        {menu:"",info:"order19"},
        {menu:"",info:"order20"},
        {menu:"",info:"order21"},
        {menu:"5.19",info:"order22"},
        {menu:"5.19",info:"order23"},
        {menu:"",info:"order24"}
      ]
    }
  ];

  //
  export default {
    data: function () {
      return {
        slideList:data,
        slideInfo:{},
        isBlock:false
      }
    },
    watch:{
      $route: function () {
        // $route 

      }
    },
    created: function () {
      // 
      // 
    },
    methods:{

    }
  }

</script>
<style>
  .sideList dd.block{display: block}
</style>



