<template>
  <div class="mt50" style="font-size: 35px;" >
    <h2 style="font-size: 30px;color: blue">BS3:Axios</h2>
    <div>
      <input type="button" value="get" @click="getData"/>
      <div>
        <span style="float: left"></span>
        <span style="float: left">{{message}}</span>
      </div>
    </div>
  </div>
</template>

<script>


  //
  export default {
    data: function () {
      return {
        message:""
      }
    },
    watch:{
      $route: function () {

      }
    },
    created: function () {
    },
    methods:{
      getData:function(){
        let This= this;
        var _url="http://easy-mock.com/mock/596b79eaa1d30433d8347de3/menu/getinfo";
        this.$http.get(_url)
          .then((response)=>{
          This.message=response.data.data;

        })
        .catch((error) =>{
          console.log(error)
        })
      }

    }
  }

</script>
