 export const Regexs = {
     /**
      * Email
      * @type RegExp
      */
     Email: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,

      /**
      * Phone
      * @type RegExp
      */
      Phone:/^1\d{10}$/,

     /**
      * Url
      * @type RegExp
      */
     Url: /(ftp|http|https):\/\/(\w+:{0,1}\w*@)?(\S+)(:[0-9]+)?(\/|\/([\w#!:.?+=&%@!\-\/]))?/,
     /**
      * 
      * @type RegExp
      */
     Chinese: /[\u4e00-\u9fa5]/,
     /**
      * 
      * @type RegExp
      */
     DoubleByte: /[^\x00-\xff]/,
     /**
      * 
      * @type RegExp
      */
     NegativeInt: /^-[0-9]*[1-9][0-9]*$/,
     /**
      * 
      * @type RegExp
      */
     NotNegativeInt: /^d+$/,
     /**
      * 
      * @type RegExp
      */
     PositiveInt: /^[0-9]*[1-9][0-9]*$/,
     /**
      * 
      * @type RegExp
      */
     NotPositiveInt: /^((-d+)|(0+))$/,
     /**
      * 
      * @type RegExp
      */
     Int: /^-?d+$/,
     /**
      * 
      * @type RegExp
      */
     NotNegativeFloat: /^d+(.d+)?$/,
     /**
      * 
      * @type RegExp
      */
     PositiveFloat: /^((0-9)+.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*.[0-9]+)|([0-9]*[1-9][0-9]*)$/,
     /**
      * 
      * @type RegExp
      */
     NotPositiveFloat: /^((-d+.d+)?)|(0+(.0+)?)$/,
     /**
      * 
      * @type RegExp
      */
     NegativeFloat: /^(-(((0-9)+.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*.[0-9]+)|([0-9]*[1-9][0-9]*)))$/,
     /**
      * 
      * @type RegExp
      */
     English: /^[A-Za-z]+$/,
     /**
      * 
      * @type RegExp
      */
     EnglishUpper: /^[A-Z]+$/,
     /**
      * 
      * @type RegExp
      */
     EnglishLower: /^[a-z]+$/,
     /**
      * +
      * @type RegExp
      */
     EnglishOrNumber: /^[A-Za-z0-9]+$/,
     /**
      * ++
      * @type RegExp
      */
     EnglishOrNumberOrUnderline: /^w+$/,
     /**
      * html
      * @type RegExp
      */
     Html: /<(.*)>.*<\/\1>|<(.*) \/>/,
     /**
      * 
      * @type RegExp
      */
     ChinaTel: /\d{3}-\d{8}|\d{4}-\d{7}/,
     /**
      * 
      * @type RegExp
      */
     ChinaPostCode: /[1-9]\d{5}(?!\d)/,
     /**
      * 
      * @type RegExp
      */
     ChinaIDCard: /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/,
     /**
      * (0~120)
      * @type RegExp
      */
     HumanAge: /^(([0-9])|([1-9][0-9])|(1[0-1][0-9])|(120))$/
 }



