<template>
<div class="mt50">
   <span style="color:red;font-size:24px;text-align:left;marginBottom:20px;display:inline-block"></span>
<el-row :gutter="20">

  <el-col :span="6">
  <div class="">
    <b>:</b>{{data.date}}
  </div>
  </el-col>
  <el-col :span="6">
  <div class="">
    <b>:</b>{{data.name}}
  </div>
  </el-col>

  <el-col :span="6">
  <div class="">
    <b>:</b>{{data.address}}
  </div>
  </el-col>
  <el-col :span="6">
  <div class="">
    <b>:</b>{{data.zip}}
  </div>
  </el-col>
<el-button style='marginTop:100px' type="primary" @click='back'></el-button>
</el-row>
</div>

</template>

<script>
  export default {
    data: function () {
      return {
        data:{}
      }
    },
    watch:{
      $route: function () {

      }
    },
    created: function () {//
        this.data = this.$route.params.data;
    },
    methods:{
     back(){
        this.$router.go(-1)
     }
    }
  }

</script>
<style>

</style>



