<template>
  <div>
    <h2></h2>
    {{message | capitalize }}
    <hr/>
    <input type="button" value="" @click="sortFn(0,'age')" style="margin-left: 20px;margin-bottom: 20px;"/>
    <input type="button" value="" @click="sortFn(1,'age')" style="margin-left: 20px;margin-bottom: 20px;"/>

    <input type="button" value="" @click="sortFn(0,'name')" style="margin-left: 20px;margin-bottom: 20px;"/>
    <input type="button" value="" @click="sortFn(1,'name')" style="margin-left: 20px;margin-bottom: 20px;"/>

    <input type="button" value="" @click="addFn" style="margin-left: 20px;margin-bottom: 20px;"/>
    <table border="1" style="margin: 0 auto">
      <thead>
      <tr>
        <th style="cursor: pointer;"></th>
        <th style="cursor: pointer"></th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="item in dataList">
        <td v-for="(val, key, index) in item">{{val}}</td>
      </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
  //
  export default {
    data: function () {
      return {
        message:"hello",
        dataList1:[],
        dataList:[
          {
            name:"liqin",
            age:12
          },
          {
            name:"daiwei",
            age:25
          },
          {
            name:"aaaa",
            age:1
          },
          {
            name:"zhaoliu1",
            age:33
          },
          {
            name:"xiaoming",
            age:13
          }
        ]
      }
    },
    watch:{
      $route: function () {

      }
    },
    created: function () {
    },
    methods:{
      addFn:function(){
        this.dataList.push({
          name:"",
          age:22
        })
      },
      sortFn: function (flag,str) {
        var temp={};
        if(flag == 0){
          temp = this.dataList[0];
          for(var i=1;i<this.dataList.length;i++){
            if(this.dataList[i][str] < this.dataList[i-1][str]){
              temp = this.dataList[i-1];
              this.dataList[i-1] = this.dataList[i];
              this.dataList[i] = temp;
              this.sortFn(0,str);//
            }else{
              temp=this.dataList[i-1]
            }
          }
        }else if(flag == 1){//temp
          temp = this.dataList[0];
          for(var i=1;i<this.dataList.length;i++){
            if(this.dataList[i][str] > this.dataList[i-1][str]){
              temp = this.dataList[i];
              this.dataList[i] = this.dataList[i-1];
              this.dataList[i-1] = temp;
              this.sortFn(1,str)
            }else{
              temp=this.dataList[i-1];
            };
          }
        }
        this.dataList = this.dataList.splice(0);//vue
        temp={};//
      }
    },
      filters:{
    capitalize: function (value) {
      value = value.toString();//toString()    toString    charAt()
      return value.charAt(0).toLocaleUpperCase() + value.slice(1)
       }
     }
  }

</script>
<style>
</style>



