<template>
    <div class="mt50" >
    <span style="font-size: 30px;color: greenyellow">vuex-- </span>
      <br/>
      <br/>
      <input type="button" value="+" @click="increment"/>
      <input type="button" value="-" @click="decrement"/>
      <input type="button" value="" @click="clickOdd"/>
      <input type="button" value="" @click="clickAsync">
      <br/>
      <br/>
      <div>
         {{count}}{{getOdd}}

      </div>
    </div>
</template>
<script>
  import { mapGetters , mapActions} from 'vuex'
  export default {
     computed: {
      //  getters  computed  
      ...mapGetters([
        'count',
        'getOdd',

      ])
      },
      methods:{
      ...mapActions([
          'increment', //  this.increment()  this.$store.dispatch('increment')
          'decrement',
          'clickOdd',
          'clickAsync'
        ])

      },
    data: function () {
      return {

      }
    },
    watch:{
      $route: function () {

      }
    },
    created: function () {
    },

  }

</script>
<style>
</style>



