<template>
  <div class="mint-ui">
    <h2>qqq</h2>
    <mt-header title=""></mt-header>
    <hr/>
    <button @click="tipMsg"></button>
    <mt-button @click.native="handleClick"> handleClick</mt-button>
    <mt-button @click.native="handleClick2">alert</mt-button>
    <hr>
    <mt-radio
      class="textLeft"
      align="left"
      title=""
      v-model="value"
      :options="options">
    </mt-radio>
    <div>
      <span>:{{value}}</span>
    </div>
    <hr/>
  </div>
</template>
<script>
  import { Toast , MessageBox ,Radio } from 'mint-ui';
export default {
  data(){
    return {
      msg:"hello",
      value:"",
      options:[
        {
          label: '',
          value: 'F',
          disabled: true
        },
        {
          label: 'A',
          value: 'A'
        },
        {
          label: 'B',
          value: 'B'
        }
      ],
    }
  },
  methods:{
    tipMsg(){
      alert("ok");
    },
    handleClick(){
      Toast({
        message: '11',
        iconClass: 'icon icon-success'
      });
    },
    handleClick2(){
      MessageBox('', '');
    }
  }
}
</script>
<style>
.textLeft{text-align: left}
</style>



