<template>
    <div>
      <div class="mt50" style="font-size: 30px;color:red">
         
      </div> 
      <input type="text" 
      placeholder="" 
      v-model="getVal">
      <drop :data='sportList' v-on:getData="handleData">
      </drop>
    </div>
</template>
<script>
  //
  // 
  import drop from "@/components/dropDown"
  export default {
    data: function () {
      return {
         getVal:'',
         sportList:[
         {
          name:'121'
         },{
          name:''
         },{
          name:''
         }]
      }
    },
    components:{
      drop,
    },
    methods:{
      handleData(val){
        this.getVal = val;
      }
    }
  }

</script>
<style>
</style>



