<template>
<div class="mt50">
    <span style="font-size: 30px;color: darkblue">
        element-ui()
    </span>
  <el-table
    :data="tableData"
    border
    style="width: 100%">

    <el-table-column
      fixed
      prop="id"
      label=""
      width="200">
    </el-table-column>

    <el-table-column
      prop="date"
      label=""
      width="200">
    </el-table-column>
    <el-table-column
      label=""
      >
       <template scope="scope">
        <span style="margin-left: 10px">{{ scope.row.name }}</span>
      </template>

    </el-table-column>
    <el-table-column
      prop="province"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="city"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="address"
      label=""
       width="380"
     >
    </el-table-column>
    <el-table-column
      prop="zip"
      label=""
      width="190">
    </el-table-column>
    <el-table-column
      fixed="right"
      label=""
      width="200">
      <template scope="scope">
        <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
        <el-button type="text" size="small"></el-button>
      </template>
    </el-table-column>
  </el-table>
    
</div>

</template>

<script>
//  ?=  
var reg = /\b\w+(?=ing\b)/g;
var str = "I am singing while you're dancing";
/*console.log(str.match(reg));*/
  export default {
    data: function () {
      return {
            tableData: [{
          id:'1',
          date: '2016-05-03',
          name: '1',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
           id:'2',
          date: '2016-05-02',
          name: '2',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
           id:'3',
          date: '2016-05-04',
          name: '3',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
           id:'4',
          date: '2016-05-01',
          name: '4',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }]
      }
    },
    watch:{
      $route: function () {

      }
    },
    created: function () {
    },
    methods:{
       handleClick(data) {
        console.log(data);
        //idparams params: { code: scope.row.busiOppoCode }    path: 'xt/editbi5/:code',
        // editbi5ajaxbusiOppoCode:this.$route.params.codeeditbi5
        this.$router.push({
          name:'editbi5',
          params:{
            data:data
          }
        })
      }
    }
  }

</script>
<style>
.el-table th{text-align: center;}
</style>



