<template>
      <div class="dropDown">
       <slot name='header'></slot>
        <ul class="oUi">
        <!-- li  -->
          <li 
          @click="pushValue(item)"
          class="hoverGreen" 
          v-for="(item, index) in sportList" 
          :key='item.name'>
            {{item.name}}
          </li>
        </ul>
        <slot name='footer'></slot>
      </div>  
</template>
<script>
  //
  export default {
    data: function () {
      return {
         sportList:[]
      }
    },
    props:["data"],
    methods:{
      pushValue(value){
        console.log(value.name);// props event slot 
        this.$emit("getData",value.name);
      },
      mouseover(index){
        
      }
    },
    mounted(){
      this.sportList = this.data;
      // 
    }
  }

</script>
<style>
.dropDown{
  width: 500px;
  margin: 0px auto;
  border:1px solid red;
}
.oUi{
overflow: hidden;
clear: both;
padding-left: 0
}
.oUi li{
   list-style: none;
   cursor: pointer;

}
.oUi li.active{
  background-color: yellow; 
}
/*//*/
.hoverGreen:hover{
  background-color: green;
}
</style>



